import { useEffect, useRef } from "react";
import Emitter from '../event.emitter';

const Event = {
  on: (event, fromMfId, fn) => Emitter.on(event, fromMfId, fn),
  off: (event, fromMfId, fn) => Emitter.off(event, fromMfId, fn),
};
Object.freeze(Event);

// React.useEffect(() => {
//   Event.on('CARD_APPLIED', '@ab/w-card-spend', someFunction);
//   return () => {
//     Event.off('CARD_APPLIED', '@ab/w-card-spend', someFunction);
//   };
// }, []);

export default function useEventListener(
  eventName,
  ownerId,
  callback,
  element = window
) {
  const callbackRef = useRef(callback)

  useEffect(() => {
    callbackRef.current = callback
  }, [callback])

  useEffect(() => {
    if (element == null) return
    const handler = e => callbackRef.current(e)
    Event.on(eventName, ownerId, handler);
    return () => Event.off(eventName, ownerId, handler)
  }, [eventName, ownerId, element])
}